$Pink:#FF99C8;
$Cream:#FCF6BD;
$LightBlue:#D0F4DE;
$Blue:#A9DEF9;
$Purple:#E4C1F9;
$White: #FFFFFF;
$Black: #231B1B;

@font-face {
font-family: 'Bukhari';
src: url('../../components//fonts/bukhari/Bukhari\ Script.ttf');
font-weight: 900;
font-style:normal;
font-stretch: normal;
}

@import url('https://fonts.googleapis.com/css2?family=Fanwood+Text&family=Nanum+Gothic+Coding&family=Noto+Sans&display=swap');

.examples{
font-family: 'Fanwood Text', serif;
font-family: 'Nanum Gothic Coding', monospace;
font-family: 'Noto Sans', sans-serif;
font-family:'Bukhari';
}

.zoom{
display:flex;
align-items: center;
justify-content: center;
flex-direction: column;
width: 100%;
}

.boxPost{
display:flex;
width: 65%;
margin-top: 2%;
margin-bottom:2%;
align-items: center;
justify-content: center;
flex-direction: column;
}

.buttonPoast{
width: 100%;
height: 4rem;
box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.3);
border-radius:10px 10px 10px 10px;
border: solid .2rem $Black;
display: flex;
align-items: center;
justify-content: center;
text-align: center;
color: $Black;
font-family: 'Noto Sans', sans-serif;
font-size: 3vh;
transition: all .3s;
position: relative;
overflow: hidden;
border: solid .2rem $Black;
font-weight: bold;
z-index: 1;
&:after {
content: '';
position: absolute;
bottom: 0;
left: 0;
width: 100%;
height: 100%;
background-color: $Blue;
border-radius: 5px 5px 5px 5px;
z-index: -2;
}
&:before {
content: '';
position: absolute;
bottom: 0;
left: 0;
width: 0%;
height: 100%;
background-color: darken($Pink, 15%);
transition: all .3s;
border-radius: 5px 5px 5px 5px;
z-index: -1;
}
&:hover {
color: $Cream;
cursor: url(../../components/images/pointer.png),default;
&:before {
width: 100%;
}
}
}

.popUp{
position: fixed;
bottom: 2%;
z-index: 10;
right: 10%;
z-index: 3;
width: 70%;
min-height: 10rem;
align-items: center;
justify-content: center;
height: fit-content;
border: solid .2rem $Black;
background-image: linear-gradient(to right top, #d0f4de, #bff0e4, #b1ecec, #a9e6f3, #a9def9);
border-radius: 10px 10px 10px 10px;
box-shadow: 20px 20px 0 rgba(0, 0, 0, 0.3);
}

.form{
display:flex;
align-items: flex-start;
justify-content: center;
width: 30%;
flex-direction: column;
margin-bottom: 3%;
}

.label{
margin-top:2%;
margin-bottom:2%;
display:flex;
align-items:flex-start;
width: 100%;
justify-content: flex-start;
flex-direction: row;
font: size 3vh;
font-family: 'Noto Sans', sans-serif;
font-weight: bold;
}

.input{
display: flex;
width: 100%;
margin-bottom:3%;
height: 2rem;
font: size 4vh;
font-weight: bold;
font-family: 'Noto Sans', sans-serif;
background-color: $White;
border: transparent;
border-radius: 10px 10px 10px 10px;
box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.3);
}

.textarea{
display: flex;
width: 100%;
margin-bottom:3%;
height: 7rem;
font: size 4vh;
resize: none;
font-weight: bold;
font-family: 'Noto Sans', sans-serif;
background-color: $White;
border: transparent;
border-radius: 10px 10px 10px 10px;
box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.3);
}

.iconButtonsForm{
max-width: 1.5rem;
min-width: 1.5rem;
max-height: 1.5rem;
min-height: 1.5rem;
margin-right: 5%;
}

.iconButtons{
max-width: 1.5rem;
min-width: 1.5rem;
max-height: 1.5rem;
min-height: 1.5rem;
}


.containerSave{
display:flex;
align-items: center;
justify-content: center;
width: 100%;
margin-top: 5%;
}

.buttonBookPop{
display: flex;
align-items: center;
justify-content: center;
text-align: center;
width: 6rem;
height: 2rem;
margin-bottom: 2%;
border-radius: 10px 10px 10px 10px;
color: $Black;
box-shadow: 6px 6px 0 rgba(0, 0, 0, 0.3);
font-family: 'Noto Sans', sans-serif;
font-size: 2vh;
margin-top: 1%;
transition: all .3s;
position: relative;
overflow: hidden;
border: transparent;
font-weight: bold;
z-index: 1;
&:after {
content: '';
position: absolute;
bottom: 0;
left: 0;
width: 100%;
height: 100%;
background-color: $Pink;
border-radius: 10px 10px 10px 10px;
z-index: -2;
}
&:before {
content: '';
position: absolute;
bottom: 0;
left: 0;
width: 0%;
height: 100%;
background-color: darken($Pink, 15%);
transition: all .3s;
border-radius: 10px 10px 10px 10px;
z-index: -1;
}
&:hover {
color: $Cream;
cursor: url(../../components/images/pointer.png),default;
&:before {
width: 100%;
}
}
}

.sortFileUSer{
width: 100%;
height: 10rem;
display: flex;
flex-wrap: wrap;
align-items: center;
justify-content: center;
border: solid .2rem $Black;
margin-top: 5%;
flex-direction: column;
border-radius: 10px 10px 10px 10px;
box-shadow: 10px 10px 0 rgba(0, 0, 0, 0.3);
margin-bottom: 5%;
}

.imageFile{
display: block;
width:100%;
height:100%;
object-fit: cover;
border-radius: 5px 5px 5px 5px;
}

.buttonX{ 
display: flex;
width: 100%;
margin-right: 3%;
margin-top: 1%;
justify-content: flex-end;
}

.buttonBack{
display: flex;
align-items: center;
justify-content: center;
text-align: center;
width: 4rem;
min-height: 2rem;
border-radius: 10px 10px 10px 10px;
color: $Black;
box-shadow: 6px 6px 0 rgba(0, 0, 0, 0.3);
font-family: 'Noto Sans', sans-serif;
font-size: 2vh;
margin-top: 1%;
transition: all .3s;
position: relative;
overflow: hidden;
border: transparent;
font-weight: bold;
z-index: 1;
&:after {
content: '';
position: absolute;
bottom: 0;
left: 0;
width: 100%;
height: 100%;
background-color: $Pink;
border-radius: 10px 10px 10px 10px;
z-index: -2;
}
&:before {
content: '';
position: absolute;
bottom: 0;
left: 0;
width: 0%;
height: 100%;
background-color: darken($Pink, 15%);
transition: all .3s;
border-radius: 10px 10px 10px 10px;
z-index: -1;
}
&:hover {
color: $Cream;
cursor: url(../../components/images/pointer.png),default;
&:before {
width: 100%;
}
}
}

@media (max-width: 1248px) {

.boxPost{
width: 100%;
margin-top: 5%;
}

.buttonPoast{
font-size: 2vh;
}

.popUp{
width: 80%;
height: fit-content;
top: 10%;
}

.form{
width: 60%;
align-items:center;
justify-content: center;
display:flex;
}

.label{
display: flex;
align-items:center;
justify-content: flex-start;
text-align:center;
width: 100%;
font-size: 2.5vh;
}

.iconButtonsForm{
min-width: 2.5rem;
max-width: 2.5rem;
max-height: 2.5rem;
min-height: 2.5rem;
}

.input{
width: 100%;
height: 4rem;
font-size: 2vh;
}

.textarea{
height: 10rem;
font-size: 1.5vh;
}

.buttonBack{
width: 6rem;
height: 4rem;
}

.iconButtons{
min-width: 2.5rem;
max-width: 2.5rem;
max-height: 2.5rem;
min-height: 2.5rem;
}

.buttonBookPop{
width: 8rem;
height: 4rem;
}


.sortFileUSer{
height: 15rem;
}

}