@import url('https://fonts.googleapis.com/css2?family=Fanwood+Text&family=Nanum+Gothic+Coding&family=Noto+Sans&display=swap');

.examples{
font-family: 'Fanwood Text', serif;
font-family: 'Nanum Gothic Coding', monospace;
font-family: 'Noto Sans', sans-serif;
font-family:'Bukhari';
}

$Pink:#FF99C8;
$Cream:#FCF6BD;
$LightBlue:#D0F4DE;
$Blue:#A9DEF9;
$Purple:#E4C1F9;
$White: #FFFFFF;
$Black: #231B1B;

.switch {
position: relative;
display: block;
width: 12rem;
height: 3.5rem;
background: $Pink;
border-radius: 10px 10px 10px 10px;
&:before {
content: "Login";
background: $Black;
border: solid .2rem $Black;
color: $Cream;
font-family: 'Noto Sans', sans-serif;
position: absolute;
top: 50%;
left: 5%;
transform: translate(-5%, -50%);
font-size: 3vh;
width: 45%;
height: 70%;
display: grid;
place-items: center;
border-radius: 10px 10px 10px 10px;
transition: all 0.6s ease;
}
&:hover{
cursor: url(../../components/images/pointer.png),default;
}
}

#checkbox {
display: none;
}

#checkbox:checked ~ .switch:before {
left: 50%;
content: "Sing Up";
}

@media (max-width: 1248px) {

.switch{
width: 35rem;
height: 10rem;
}
}